<template>
  <section class="faq" :class="{ gray }">
    <h3 class="faq__title text-gradient">
      <b>Ответы</b>
      &nbsp;на частые вопросы
    </h3>

    <ul class="faq__list">
      <li
        v-for="(item, i) in faqList"
        :key="i"
        class="faq__item"
        :class="{ active: currentIndex === i }"
      >
        <h4 class="faq__item-title">
          {{ item.question }}
        </h4>
        <button type="button" class="faq__item-close" @click="onClickFaq(i)" />
        <div class="faq__item-content" v-html="item.answer" />
      </li>
    </ul>
  </section>
</template>

<script>
export default {
  name: 'FAQ',
  props: {
    gray: Boolean,
    faqList: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      currentIndex: null
    };
  },
  methods: {
    onClickFaq(index) {
      if (this.currentIndex === index) {
        this.currentIndex = null;
        return;
      }

      this.currentIndex = index;
    }
  }
};
</script>

