<template>
  <section class="questions-section" :class="{ gray }">
    <div class="container">
      <div class="questions-wrapper">
        <div class="questions-heading">
          <h3 class="questions-title text-gradient">
            Остались вопросы?
          </h3>
        </div>
        <div class="questions left-box">
          <LazyLoading
            url="/img/questions.png"
            alt="Как работает сервис грузоперевозок MoreCargo"
            class="questions-img"
          />
          <div class="questions__info">
            <h4>
              <strong class="text-gradient">MoreCargo</strong>
              <span data-dash="–" class="questions__dash" v-html="text" />
            </h4>
            <nuxt-link
              :to="`/how-it-works/${type}/1`"
              class="md-raised gradient questions__btn"
            >
              Как это работает?
            </nuxt-link>
          </div>
        </div>
        <div class="questions right-box">
          <FeedbackForm />
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import LazyLoading from 'common/components/LazyLoading';
import FeedbackForm from '@/components/FeedbackForm';
export default {
  name: 'Questions',
  components: {
    FeedbackForm,
    LazyLoading
  },
  props: {
    gray: Boolean,
    type: {
      type: String,
      required: true,
      validator(value) {
        return ['carrier', 'shipper'].includes(value);
      }
    }
  },
  computed: {
    text() {
      if (this.type === 'shipper') {
        return 'поиск автомобиля <br /> для вашего груза';
      }

      if (this.type === 'carrier') {
        return 'с нами поиск грузов <br /> стал проще';
      }

      return '';
    }
  }
};
</script>

