<template>
  <section class="owners">
    <LazyLoading
      :url="banner"
      :alt="`Сервис грузоперевозок MoreCargo – преимущества для ${typeName}`"
      class="owners__bg"
    />

    <div class="container">
      <p class="owners__heading text-gradient">
        <span>MoreCargo</span>
        <br />
        для {{ typeName }} это
      </p>

      <FeaturesList :list="list" />
    </div>
  </section>
</template>

<script>
import LazyLoading from 'common/components/LazyLoading';
import FeaturesList from '@/components/FeaturesList';

export default {
  name: 'Owners',
  components: {
    LazyLoading,
    FeaturesList
  },
  props: {
    type: {
      type: String,
      required: true,
      validator(value) {
        return ['carrier', 'shipper'].includes(value);
      }
    },
    list: {
      type: Array,
      default: () => []
    }
  },
  computed: {
    isCarrier() {
      return this.type === 'carrier';
    },

    isShipper() {
      return this.type === 'shipper';
    },

    banner() {
      if (this.isShipper) {
        return '/img/bg-cargo-owners_2.jpg';
      }

      if (this.isCarrier) {
        return '/img/bg-cargo-owners.jpg';
      }

      return '';
    },

    typeName() {
      if (this.isShipper) {
        return 'грузовладельцев';
      }

      if (this.isCarrier) {
        return 'перевозчиков';
      }

      return '';
    }
  }
};
</script>

