<template>
  <form class="feedback" @submit.prevent="onSubmit">
    <div class="feedback__item feedback__item--full">
      <transition name="fade" mode="out-in">
        <h3 v-if="showText" class="feedback__title">
          <span class="text-gradient">Напишите нам</span>
        </h3>
        <alert v-else :message="alertMessage" class="alert" :type="alertType" />
      </transition>
    </div>
    <div class="feedback__item">
      <plain-input v-model="name" label="Имя" class="feedback__name" />
    </div>
    <div class="feedback__item">
      <plain-input
        v-model="mail"
        :error-text="mailError"
        label="Электронная почта"
        class="feedback__email"
        @input="mailError = ''"
      />
    </div>
    <div class="feedback__item feedback__item--full">
      <plain-input
        v-model="message"
        type="textarea"
        label="Сообщение"
        auto-grow
        rows="1"
      />
    </div>
    <div v-if="isMobile !== null" class="feedback__item feedback__item--full">
      <recaptcha
        :data-size="isMobile ? 'compact' : 'normal'"
        @error="onError"
        @success="onSuccess"
        @expired="onExpired"
      />
    </div>
    <div class="feedback__item feedback__item--full">
      <plain-button
        :disabled="isDisabledButton"
        :loading="isLoading"
        class="md-raised gradient feedback__submit"
        type="submit"
      >
        Отправить
      </plain-button>
    </div>
    <p class="feedback__copy">
      Отправляя сообщение, Вы соглашаетесь с обработкой персональных данных в
      соответствии с
      <a href="/legal.pdf">Политикой конфиденциальности</a>
    </p>
  </form>
</template>

<script>
import { mapGetters } from 'vuex';
import { isCorrectEmail } from 'common/helpers';
import axios from 'common/helpers/httpService';
import Alert from 'common/components/Alert.vue';
import PlainInput from 'common/components/PlainInput';

export default {
  name: 'FeedbackForm',
  components: {
    Alert,
    PlainInput
  },
  data: () => ({
    name: '',
    mail: '',
    message: '',
    isValidCaptcha: false,
    formIsValid: false,
    showText: true,
    alertMessage: '',
    alertType: 'positive',
    isMobile: null,
    isLoading: false,
    mailError: ''
  }),
  computed: {
    ...mapGetters('errors', ['getError']),
    formData() {
      const { name, mail, message } = this;
      return { name, mail, message };
    },
    isDisabledButton() {
      return (
        !this.isValidCaptcha ||
        !this.name.length ||
        !this.mail.length ||
        !this.message.length
      );
    }
  },
  created() {
    this.messageSend = 'Сообщение отправлено! Мы обязательно свяжемся с Вами!';
    this.messageError =
      'Вы можете отправлять не больше одного сообщения в минуту!';
  },
  mounted() {
    this.isMobile =
      document.body.clientWidth < 600 || document.body.clientHeight < 600;
  },
  methods: {
    async onSubmit(data) {
      if (!isCorrectEmail(this.mail)) {
        this.mailError = 'Некорректный адрес email';
        return;
      }
      try {
        this.isLoading = true;
        const postData = `name=${encodeURI(this.name)}&mail=${encodeURI(
          this.mail
        )}&message=${encodeURI(this.message)}`;
        await axios.post('/feedback', postData);
        this.alertMessage =
          'Сообщение отправлено! Мы обязательно свяжемся с Вами!';
        this.alertType = 'positive';
        this.name = '';
        this.mail = '';
        this.message = '';
      } catch (error) {
        this.alertMessage = this.getError({ error: error.message });
        this.alertType = 'danger';
      } finally {
        this.isLoading = false;
        this.showAlert();
      }
    },
    showAlert(type) {
      this.showText = !this.showText;
      this[type] = true;
      setTimeout(() => {
        this.showText = !this.showText;
        this[type] = false;
      }, 3000);
    },
    onSuccess(token) {
      this.isValidCaptcha = true;
    },
    onExpired() {
      this.isValidCaptcha = false;
    },
    onError(error) {
      console.error('Error happened:', error);
    }
  }
};
</script>

